import React, { Fragment, useState, useEffect } from 'react';

import addMonths from 'date-fns/add_months';
import isWithinRange from 'date-fns/is_within_range';

import { determineAlreadySelectedTripDates } from '../helpers/dateUtils';

import Spinner from './Spinner.react';
import TimeSlicePicker from './TimeSlicePicker.react';
import LocationMap from './LocationMap.react';
import TripCard from './TripCard.react';
import NewTrip from './NewTrip.react';

const TripList = ({ user, activeUser, locations, isFetching }) => {
  //
  // State for component
  //
  const [dateSelection, setSelectedDates] = useState([
    new Date(),
    addMonths(new Date(), 3)
  ]);

  //
  // Make sure the date selection is still valid after changes to
  // the locations list
  //
  // useEffect(
  //   () => {
  //     if (
  //       locations.length > 0 &&
  //       isBefore(new Date(), locations[0].start_date)
  //     ) {
  //       setDateSlider([
  //         getMonth(locations[0].start_date),
  //         getYear(locations[0].start_date)
  //       ]);
  //     } else {
  //       setDateSlider([getMonth(new Date()), getYear(new Date())]);
  //     }
  //   },
  //   [getMonthsInRange(locations)]
  // );

  //
  // Loading states
  //
  if (!locations || isFetching) {
    return <Spinner />;
  }
  if (locations.length === 0) {
    return (
      <div>
        No trips yet...
        <br />
        {user.id === activeUser && (
          <Fragment>
            <hr />
            <h4>ADD NEW TRIP</h4>
            <NewTrip trips={locations} />
          </Fragment>
        )}
      </div>
    );
  }

  // Filter trips based on date selection
  const filteredLocations = locations.filter(isWithinMonth(dateSelection));

  // Disable adding dates that overlap with existing trips
  const disabledDates = determineAlreadySelectedTripDates(locations);

  return (
    <Fragment>
      {user.id === activeUser && (
        <Fragment>
          <h4>ADD NEW TRIP</h4>
          <NewTrip trips={locations} />
          <hr />
        </Fragment>
      )}

      <LocationMap locations={filteredLocations} />
      <div className="uk-margin uk-flex">
        <TimeSlicePicker value={dateSelection} onChange={setSelectedDates} />
      </div>

      <hr />

      <h4>TRIPS</h4>
      {filteredLocations.map(trip => (
        <TripCard
          key={trip.id}
          disabledDates={disabledDates}
          {...trip}
          viewOnly={user.id !== activeUser}
        />
      ))}
    </Fragment>
  );
};

const isWithinMonth = dateSelection => ({ start_date, end_date }) => {
  const [start, end] = dateSelection;

  return (
    isWithinRange(start_date, start, end) || isWithinRange(end_date, start, end)
  );
};

export default TripList;
