import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';

import format from 'date-fns/format';

import List from 'react-virtualized/dist/commonjs/List';
import ReactCountryFlag from 'react-country-flag';
import Select from 'react-select';
import DatePicker from './DatePicker.react';

import {
  determineNextFreeDate,
  determineAlreadySelectedTripDates
} from '../helpers/dateUtils';
import { createTrip, fetchCities } from '../actions';

//
// Virtualized Select Component
//
const MenuList = props => {
  const rows = props.children;
  const rowRenderer = ({ key, index, isScrolling, isVisible, style }) => (
    <div key={key} style={style}>
      {rows[index]}
    </div>
  );

  return (
    <List
      style={{ width: '100%', flexGrow: true }}
      width={props.width}
      height={300}
      rowHeight={35}
      rowCount={rows.length || 0}
      rowRenderer={rowRenderer}
    />
  );
};

const convertToSelectOpts = i =>
  i.map(i => ({
    label: (
      <div>
        <ReactCountryFlag code={i.country_code} svg /> {i.name}
      </div>
    ),
    value: `${i.name}:::${i.id}`
  }));

function NewTrip({ cities, trips, createTrip, fetchCities }) {
  // Default to the next free day and no city selected
  const [dates, setDates] = useState(determineNextFreeDate(trips, 4));
  const [city, setCity] = useState(null);

  //
  // Make sure the default dates stay up to date when a new trip is created
  //
  useEffect(
    () => {
      setDates(determineNextFreeDate(trips, 4));
    },
    [trips.map(t => t.id).join('-')]
  );

  // Load cities list
  useEffect(() => {
    if (!cities.isFetching && !cities.hasFetched) {
      fetchCities();
    }
  }, []);

  //
  // Package up the state as a trip object and pass it back up
  //
  const onCreateTrip = e => {
    e.preventDefault();
    createTrip({
      city: parseInt(city.value.split(':::')[1], 10),
      start_date: format(dates[0], 'YYYY-MM-DD'),
      end_date: format(dates[1], 'YYYY-MM-DD')
    });
    setCity(null);
  };

  // Order cities by population for now
  const citiesList = Object.keys(cities.all).map(cId => cities.all[cId]);
  citiesList.sort((a, b) => b.population - a.population);

  return (
    <form className="uk-tile new-trip-form uk-padding-small uk-flex">
      <div>
        <Select
          styles={{
            container: () => ({
              width: 250
            }),
            menu: provided => ({
              ...provided,
              zIndex: 9999,
              top: null
            })
          }}
          components={{
            MenuList: props => <MenuList {...props} width={window.innerWidth} />
          }}
          placeholder="New Trip Location"
          className="country-picker"
          isSearchAble
          isLoading={cities.isFetching}
          value={city}
          onChange={setCity}
          options={convertToSelectOpts(citiesList)}
        />
      </div>
      <DatePicker
        className="uk-width-1-3@m"
        rangeClassName="uk-text-center"
        disabledDates={determineAlreadySelectedTripDates(trips)}
        value={dates}
        onChange={setDates}
        hideDaysCount
      />
      <button
        className="uk-button uk-button-secondary"
        onClick={onCreateTrip}
        disabled={city === null || dates.length !== 2}
      >
        <span className="icono-plus" />
      </button>
    </form>
  );
}

const mapStateToProps = ({ cities }) => ({
  cities
});

export default connect(
  mapStateToProps,
  {
    createTrip,
    fetchCities
  }
)(NewTrip);
