import React, { Fragment } from 'react';

import getMonth from 'date-fns/get_month';
import getDate from 'date-fns/get_date';
import differenceInDays from 'date-fns/difference_in_days';

const MONTHS = [
  'JAN',
  'FEB',
  'MAR',
  'APR',
  'MAY',
  'JUN',
  'JUL',
  'AUG',
  'SEP',
  'OCT',
  'NOV',
  'DEC'
];

const STYLES = {
  margin: 'auto',
  width: 'fit-content'
};

const DateRange = ({ start_date, end_date, hideDaysCount, className }) => (
  <div
    data-toggle
    className={`uk-flex date-range ${className || ''}`}
    style={STYLES}
  >
    <div>
      <b>
        {getDate(start_date)} <br /> {MONTHS[getMonth(start_date)]}
      </b>
    </div>
    <div>
      <b>
        {end_date && (
          <Fragment>
            {getDate(end_date)}
            <br />
            {MONTHS[getMonth(end_date)]}
          </Fragment>
        )}
      </b>
    </div>
    {!hideDaysCount && (
      <h6
        style={{
          width: 92,
          height: 42,
          textAlign: 'center',
          color: '#666',
          margin: 2,
          marginTop: 10
        }}
      >
        {end_date ? differenceInDays(end_date, start_date) : '?'} Day Trip
      </h6>
    )}
  </div>
);

export default DateRange;
